/* **************** ARTICLE CALENDAR ****************** */
.article-calendar {
  background: transparent;
  border-radius: 0;
  min-height: 255px;
  box-shadow: 1px 3px 3px 0 rgba(0, 0, 0, 0.2);
}
.article-calendar .ui-datepicker {
  background: #fff;
  border: 0;
  font-family: 'Roboto', Arial, sans-serif;
  padding: 0;
  width: 100%;
}
.article-calendar .ui-datepicker-header {
  background: #00b2b2;
  border: 0;
  border-radius: 0;
  padding: 30px 15px;
}
.article-calendar .ui-datepicker-title {
  color: #bbffff;
  font-size: 14px;
  text-transform: none;
}
.article-calendar .ui-datepicker-prev,
.article-calendar .ui-datepicker-next {
  background: transparent;
  border-radius: 50%;
  border: 1px solid #bbffff;
  height: 30px;
  width: 30px;
  position: absolute;
  top: 50%;
  transform: translate(0, -50%);
  transition: all 0.2s ease-in-out;
}
.article-calendar .ui-datepicker-prev:focus,
.article-calendar .ui-datepicker-next:focus {
  background-color: #00bbb3;
}
.article-calendar .ui-datepicker-prev:hover,
.article-calendar .ui-datepicker-next:hover {
  background-color: #049c96;
}
.article-calendar .ui-datepicker-prev::before,
.article-calendar .ui-datepicker-next::before {
  content: '';
  display: inline-block;
  width: 10px;
  height: 10px;
  border-top: 2px solid #bbffff;
  border-right: 2px solid #bbffff;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-66%, -50%) rotate(45deg);
}
.article-calendar .ui-datepicker-prev > span,
.article-calendar .ui-datepicker-next > span {
  display: none;
}
.article-calendar .ui-datepicker-prev {
  left: 15px;
  transform: translate(0, -50%) rotate(180deg);
}
.article-calendar .ui-datepicker-next {
  right: 15px;
}
.article-calendar .ui-datepicker-calendar {
  border: 1px solid #bbdfeb;
  margin: 0;
}
.article-calendar th {
  color: #4bb9ae;
  font-size: 14px;
  padding: 15px 6px;
  text-align: center;
}
.article-calendar td {
  padding: 7px 6px;
  text-align: center;
}
.article-calendar .ui-state-default {
  background: transparent;
  border-radius: 50%;
  border: 1px solid transparent;
  color: #717171;
  font-size: 13px;
  display: inline-block;
  text-align: center;
  transition: all 0.2s ease-in-out;
  line-height: 22px;
  height: 28px;
  width: 28px;
}
.article-calendar .ui-state-default.ui-state-highlight {
  border-color: #717171;
}
.article-calendar .ui-state-default.ui-state-active {
  background: #eee;
}
